@import url('https://fonts.googleapis.com/css2?family=Instrument+Serif&family=Roboto:wght@100;300&display=swap');

body {
    background-image: linear-gradient(to bottom, lightblue,white);
    background-attachment: fixed;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto';
    color: #04045b !important;
    // position: relative;
    // top: -10px;
}

nav {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background-color: rgb(132, 203, 227);
    opacity: 20;
    display: flex;
    justify-content: space-between;
    z-index: 1;
    // justify-content: space-evenly;
}

.logo {
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.socials {
    margin-top: 1.5vh;
}
img {
    max-width: 80%;
    max-height: 80%;
}
.logo-img {
    margin-left: 4vw;
}

#linkedin {
    margin-right: 4vw;
    margin-left: 2vw;
}

h1 {
    margin-bottom:0vh;
    margin-left: 0.4vw;
    font-size: 150%;
    font-weight: bolder;
}

h2 {
    text-align: center;
    font-size: 140;
}

aside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 3vh 2vw 3vh 2vw;
}

.form-container {
    margin: 5vh 4vw 5vh 4vw;
    width: 22vw;
    height: 80vh;
    position: fixed;
    background-color:rgb(161, 214, 231);
    border-radius: 1.4em;
}

#selections-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 50%;
}

.content {
    display: flex;
    flex-direction: row;
    position: relative;
    top: 10vh;
    overflow: hidden;
}

.chart-container {
    position: relative;
    left: 26vw;
    margin: 5vh 4vw 5vh 4vw;
    width: 66vw;
}

.zip-code-input {
    width: 70%;
    height: 4vh;
}

li {
    margin: 0 2vw 1vh 2vw;
    height: 4vh;
}

ul {
    margin-top: 2vh;
    padding: 0;
}

#zip-container {
    height: 37%;
}

input {
    border: #04045b solid 0.5px;
    border-radius: 2em;
    text-align: center;
    // width: 80%;
}

option {
    color: #04045b;
}

#form-button, #add-zip {
    background-color: #04045b !important;
    border-radius: 2em;
    color: #EDF2FB;
    height: 4vh;
}

.delete-button {
    background-color: #04045b !important;
    border-radius: 2em;
    color: #EDF2FB;
    height: 3vh;
    font-size: 60%;
}

.zip-container {
    // background-color: lightblue;
    display: flex;
    justify-content: space-evenly;
    width: 80%;
    align-items: center;
    border-radius: 2em;
    height: 4vh;
}

#select-container {
    border-radius: 2em;
    display: flex;
    overflow: hidden;
    background: white;
    border: 0.5px solid #04045b;
    justify-content: flex-end;
    height: 4vh;
}

.select-box {
    border: 0px;
    outline: none;
    text-align: center;
    display: block;
    width: 20vw;
    height: 4vh;
}


.date-input {
    display: block;
    width: 100%;
    height: 4vh;
}

#enter-zip-container {
    display: flex;
    justify-content: space-between;
}

canvas {
    background-color: white;
    margin-bottom: 3vh;
    border-radius: 1.4em;
    padding: 2vh;
}

.units {
    display: flex;
    justify-content: space-between;
}

#zip_1 {
    background-color: #966B9D;
}

#zip_2 {
    background-color: #C98686;
}
#zip_3 {
    background-color: #F2B880;
}

#zip_4 {
    background-color: #E7CEBC;
}

